.product-details {
    &__wrapper {
        overflow-x: hidden;
        position: relative;

        margin-top: 51px;
    }

    &__inner {
        margin-top: 120px;
        padding: 0 0 0 10px;
    }

    &__title {

        font-size: 20px;
        font-weight: 700;

        color: var(--text-color);
        text-transform: uppercase;

        &--sm {
            font-size: 20px;
        }

        &--red {
            color: $red-color;
        }
    }

    &__text {
        font-weight: 500;
        font-size: 18px;

        color: var(--same-color);

        &--mt {
            margin-top: 15px;
        }
    }

    &__text-item {
        margin-top: 15px;
    }

    &__text-inner {
        margin: 100px 0;

        &--sm {
            margin-bottom: 40px;
        }

        & p {
            margin-top: 15px;

            font-size: 18px;
            font-weight: 500;
            color: var(--same-color);
        }
    }

}

@include rmin($tablet) {
    .product-details {
        &__wrapper {
            overflow-x: unset;
        }
    }
}

@include rmin($desktop) {
    .product-details {
        &__wrapper {
            margin-right: 0;
        }

        &__title {
            font-size: 35px;

            &--sm {
                font-size: 28px;

            }
        }

        &__text {
            font-size: 20px;
        }

        &__text-inner {
            min-height: 250px;
            width: 100%;
            max-width: 812px;

            margin: 200px 0;

            &--sm {
                width: 100%;
                max-width: 588px;

                // margin-bottom: 100px;
            }

            & p {
                margin-top: 30px;

                font-size: 20px;
            }
        }

        &__text-item {
            margin-top: 30px;
        }

        &__inner {
            display: flex;

            width: 100%;
            margin-left: 150px;
            margin-top: 0;
            padding: 0;
        }

        &__main-content {
            position: relative;
            width: 100%;
            height: 100%;
        }

    }
}

@include rmin($desktop-s) {
    .product-details {
        &__text-inner {
            width: 55%;
        }
    }
}
