// projects slider
.slider-projects__text-wrapper,
.slider-projects__description,
.slider-projects__tag {
    transform: translateY(35px);
    transition: transform .8s, opacity .5s;
    opacity: 0;
}

.slider-projects__description-wrapper,
.slider-projects__img-wrapper {
    opacity: 0;
}

.glide__slide--active {

    // project cover
    .slider-projects__img-wrapper {
        animation: slide-in-bottom 1s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
    }

    // project description text
    .slider-projects__description-wrapper {
        opacity: 1;

        h3 {
            opacity: 0;
            animation: focus-in-contract 1s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
            animation-delay: 1s;
        }

        .slider-projects__description,
        .slider-projects__tag {
            transform: translateY(0);
            transition-delay: 1.5s;
            opacity: 1;
        }
    }

    // project title & link
    .slider-projects__text-wrapper {
        transform: translateY(0);
        transition-delay: 1.2s;
        opacity: 1;
    }
}

// slider bullets
.glide__bullets {
    transform: translateY(0);
    opacity: 1;
}

.contacts__submit-btn:hover,
.site-header__contacts-order:hover {
    animation: pulsate-fwd 0.6s ease-in-out infinite both;
}

@include rmin($tablet) {
    .glide__slide--active {
        .slider-projects__text-wrapper {
            transition-delay: 1.2s;
        }
    }
}

@include rmin($desktop) {
    .glide__slide--active {
        .slider-projects__text-wrapper {
            min-width: 550px;
            transition-delay: 2.15s;
        }
    }
}
