.slider-projects {
    height: 100vh;

    &__wrapper {
        position: relative;
    }

    .glide__slide {
        height: 100vh;

        h2,
        h3 {
            font-size: 6vmin;

            &:first-letter {
                text-transform: uppercase;
                color: $red-color;
            }
        }

        h2 {
            color: $white-color;
            font-size: 9vmax;
            // -webkit-text-stroke: var(--stroke);
        }
    }

    .glide__track {
        position: absolute;
        width: 100%;
    }

    .glide__bullets {
        bottom: 20%;
        overflow-x: auto;
        width: 100%;

        left: 0;

        z-index: 0;
        transform: unset;
        white-space: nowrap;
    }

    .glide__bullets::-webkit-scrollbar {
        display: none;
    }

    .glide__bullet {
        width: 200px;
        padding: 10px 5px;
        margin: 0.3em;
        font-size: 28px;
        color: var(--same-color);

        font-family: $main-text;

        border-radius: unset;
        height: unset;
        background-color: unset;
        border: unset;

        box-shadow: unset;

        &:hover,
        &:focus {
            color: var(--text-color);
        }

        &--active {
            color: $white-color !important;
            border: unset;
        }
    }

    .glide__arrows {
        position: absolute;
        z-index: 1;
        bottom: 15%;
        left: -40px;
    }

    .glide__arrow {
        fill: var(--same-color);
        border: unset;
        box-shadow: unset;

        &-icon {
            width: 20px;
            height: 20px;
        }

        &--left {
            right: 0;
            left: unset;
        }

        &--right {
            left: 0;
            right: unset;
        }
    }

    &__description-wrapper {
        display: none;
    }

    &__text-wrapper {
        padding-top: unset;
        position: absolute;
        z-index: 1;

        width: 100%;
        top: 40%;

        display: flex;
        flex-direction: column;
        align-items: center;
    }

    &__img-wrapper {
        position: absolute;
        width: 100vw;
        height: 100vh;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;

            object-position: 50% 50%;
        }

        &::before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            background-color: var(--substrate);

            z-index: 1;

            width: 100%;
        }
    }
}

@include rmin($tablet) {
    .slider-projects {
        .glide__slide {
            h2 {
                font-size: 10vmax;
            }
        }

        .glide__bullet {
            width: 100%;

            padding-top: 25px;
            padding-bottom: 25px;

            margin: 0 25px 15px;
            font-size: 40px;
        }
    }
}

@include rmin($desktop) {
    .slider-projects {
        height: calc(100vh - 165px);

        .glide__slide {
            display: flex;
            align-items: center;
            flex-direction: row;
            padding: 0 20px;
            height: calc(100vh - 105px);
            padding: unset;

            h2 {
                font-size: 8vmax;
                -webkit-text-stroke: var(--stroke);
                color: var(--text-color);
            }
        }

        .glide__bullet {
            font-size: 3.5vmin;
            margin: 0 20px 15px;

            &--active {
                color: var(--text-color) !important;
            }
        }

        .glide__bullets {
            width: 45%;
            left: 0;
            overflow: scroll;
            bottom: 10%;
        }

        &__description-wrapper {
            display: block;
            width: 50%;
            margin-top: 20px;
            margin-bottom: auto;
            // padding-left: 30px;
            padding-right: 100px;
        }

        &__description {
            color: var(--same-color);
            font-size: 2.5vmin;
            line-height: 1.4;
            margin-top: 10px;
        }

        &__tag {
            color: var(--same-color);
            font-weight: 700;
            font-size: 1.5vmin;
            text-transform: uppercase;
        }

        &__text-wrapper {
            padding-top: unset;
            width: unset;

            position: absolute;
            left: -35%;
            top: 40%;
            // transform: translateY(-30%);

            // align-items: flex-end;
        }

        &__img-wrapper {
            position: relative;
            width: 50%;
            height: 100%;

            img {
                height: 100%;
                object-fit: cover;
            }
        }
    }
}

@include rmin($desktop-lg) {
    .slider-projects {
        .glide__bullet {
            margin: 0 35px 15px;
        }

        .glide__bullets {
            bottom: 15%;
        }

        .glide__arrows {
            bottom: 18%;
        }

        &__description-wrapper {
            margin-top: 50px;
            padding-right: 12vmin;
        }

        &__description {
            margin-top: 20px;
            font-size: 2.8vmin;
        }
    }
}
