.site-menu__nav-toggler {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2;
  display: flex;
  align-items: center;
  width: 60px;
  height: 60px;
  font-size: 24px;
  line-height: 24px;
  text-transform: uppercase;
  background-color: transparent;
  touch-action: manipulation;
}

.site-menu__nav-toggler .shape {
  position: absolute;
  left: 15px;
  top: 50%;
  width: 30px;
  height: 18px;
  color: var(--text-color);
  transform: translateY(-50%);
}

.site-menu__nav-toggler .line {
  width: 100%;
  height: 3px;
  margin: 0 0 6px;
  background-color: currentColor;
  transform-origin: right center;
  transform: scaleX(0);
  will-change: transform;
}

.site-menu__nav-toggler .line:nth-child(2) {
  max-width: 55%;
  margin-left: auto;
}

.site-menu__nav-toggler .close {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 35px;
  height: 35px;
  color: #d21e34;
  transform: translate(-50%, -50%) rotate(0) scale(0);
}

.site-menu__nav-toggler .close::before, .site-menu__nav-toggler .close::after {
  content: '';
  position: absolute;
  left: 50%;
  top: 50%;
  background: currentColor;
}

.site-menu__nav-toggler .close::before {
  width: 3px;
  height: 25px;
  margin: -12px 0 0 -1px;
}

.site-menu__nav-toggler .close::after {
  width: 25px;
  height: 3px;
  margin: -1px 0 0 -12px;
}

