}

.product-details__text-item {
  margin-top: 15px;
}

.product-details__text-inner {
  margin: 100px 0;
}

.product-details__text-inner--sm {
  margin-bottom: 40px;
}

.product-details__text-inner p {
  margin-top: 15px;
  font-size: 18px;
  font-weight: 500;
  color: var(--same-color);
}

@media only screen and (min-width: 760px) {
  .product-details__wrapper {
    overflow-x: unset;
  }
}

@media only screen and (min-width: 1023px) {
  .product-details__wrapper {
