.capitalize {
  position: relative;
  min-height: 200px;
  white-space: nowrap;
  overflow: hidden;
}

.capitalize__item {
  position: absolute;
  display: inline-block;
  padding-left: 100%;
  font-size: 80px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 2px;
  -webkit-text-stroke: 1px var(--light-gray-color);
  -webkit-text-fill-color: var(--bg-color);
  -webkit-animation: scroll 10s infinite linear;
  animation: scroll 16s infinite linear;
  animation-delay: 2s;
}

.capitalize__item--red {
  display: inline-block;
  -webkit-text-stroke: 1px #d21e34;
  -webkit-text-fill-color: #d21e34;
}

@media only screen and (min-width: 760px) {
  .capitalize__item {
    font-size: 200px;
    letter-spacing: 5px;
  }
}

@media only screen and (min-width: 1023px) {
  .capitalize {
    min-height: 600px;
    margin: 0 -150px 0 -20px;
  }
  .capitalize__item {
    font-size: 600px;
    font-weight: 700;
    letter-spacing: 5px;
    -webkit-text-stroke: 5px var(--light-gray-color);
  }
  .capitalize__item--red {
    -webkit-text-stroke: 5px #d21e34;
  }
}

@-webkit-keyframes scroll {
  0% {
    -webkit-transform: translate(0, 0);
  }
  100% {
    -webkit-transform: translate(-100%, 0);
  }
}

@keyframes scroll {
  0% {
    transform: translate(0, 0);
  }
  100% {
    transform: translate(-100%, 0);
  }
}

.cards {
  z-index: 1;
  display: flex;
  justify-content: center;
  margin-bottom: 200px;
}

.cards__inner {
  opacity: 0;
  width: 75%;
  margin: -140px auto 0;
  transform: translateY(15%);
  animation: fadeIn 3s;
  animation-delay: 400ms;
  animation-fill-mode: forwards;
}

.cards__item {
  box-shadow: 0px 5px 50px rgba(0, 0, 0, 0.25);
}

.cards__item img {
  display: block;
}

.cards__item:first-child {
  z-index: 1;
  position: relative;
  transform: rotate(-7deg) translateY(-15%);
}

.cards__item:last-child {
  position: absolute;
  transform: rotate(11deg) translateX(20%);
}

@media only screen and (min-width: 760px) {
  .cards__inner {
    margin: -80px auto 0;
  }
}

@media only screen and (min-width: 1023px) {
  .cards__inner {
    margin: -200px auto 0;
  }
  .cards__item:first-child {
    transform: unset;
    animation: leftCard 2s;
    animation-delay: 1.5s;
    animation-fill-mode: forwards;
  }
  .cards__item:last-child {
    transform: unset;
    animation: rightCard 2s;
    animation-delay: 1.5s;
    animation-fill-mode: forwards;
  }
}

@media only screen and (min-width: 1280px) {
  .cards {
    position: sticky;
    top: 20%;
    height: auto;
    margin-bottom: unset;
  }
  .cards__inner {
    position: absolute;
    top: 0;
    right: 0;
    width: 30%;
    height: 95%;
    margin: 0 auto;
    transform: unset;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes leftCard {
  0% {
    transform: rotate(0deg) translateY(0%);
  }
  100% {
    transform: rotate(-7deg) translateY(-15%);
  }
}

@keyframes rightCard {
  0% {
    transform: rotate(0deg) translateY(0%);
  }
  100% {
    transform: rotate(11deg) translateX(20%);
  }
}

.action-blocker {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 5;
  width: 100vw;
  pointer-events: none;
  will-change: transform, background-color;
}

.action-blocker.active {
  pointer-events: all;
}

.action-blocker.animate {
  background-color: var(--text-color);
}
